"use client";
import Image from "next/image";
import React, { useState } from "react";
import homeImg from "@/public/assets/imgs/Home Page.jpg";
import Input from "@/app/components/ui/Input";
import { MdOutlineLocationOn } from "react-icons/md";
import { COLORS, TEXT_STYLES } from "@/app/constants";
import { FaStar } from "react-icons/fa";
import userPic from "@/public/assets/imgs/Peronsal Image.png";
import Button from "@/app/components/ui/Button";

// Redux
import { useDispatch, useSelector } from "react-redux";
import { RootState } from "@/app/store";
import { addToCart, removeFromCart, increaseQuantity, decreaseQuantity } from "@/app/store/slices/cartSlice"

const Page = () => {
  const [activeTab, setActiveTab] = useState<"details" | "drinks">("details");

  const dispatch = useDispatch();
  const cartItems = useSelector((state: RootState) => state.cart.items);

  const drinksData = [
    { name: "فانيليا سويت كوفي", price: "47.50", id: 1 },
    { name: "كابتشينو", price: "55.00", id: 2 },
    { name: "لاتيه", price: "52.00", id: 3 },
    { name: "إسبريسو", price: "40.00", id: 4 },
  ];

  const totalCost = cartItems
    .reduce((sum, item) => sum + parseFloat(item.price) * item.quantity, 0)
    .toFixed(2);

  const reviews = [
    {
      name: "انس هشام",
      title: "مهندس برمجيات",
      rating: 5,
      comment:
        "كويس للشغل، النت سريع والمقاعد مريحة. في كهربا كتير، بس محتاجين يضيفوا حجز أونلاين للطاولات.",
      id: 1,
    },
    {
      name: "انس هشام",
      title: "مهندس برمجيات",
      rating: 4,
      comment:
        "الخدمة ممتازة والمكان واسع. بس محتاجين يزودوا خيارات العصاير.",
      id: 2,
    },
  ];

  return (
    <div className="px-4 py-20 text-right">
      {/* Hero Image */}
      <div className="flex justify-center">
        <Image src={homeImg} alt="Home" height={250} className="rounded-3xl" />
      </div>

      {/* Location Input */}
      <div className="mt-6 flex items-center justify-end gap-2">
        <MdOutlineLocationOn size={20} color={COLORS.BRAND_PRIMARY} />
        <span style={{ ...TEXT_STYLES.READEX_PRO.MEDIUM.H6, color: COLORS.TEXT }}>
          المعادي - شارع النصر
        </span>
      </div>

      {/* Tabs */}
      <div className="flex gap-4 mt-8 border-b border-gray-200">
        {["details", "drinks"].map((tab) => (
          <div
            key={tab}
            onClick={() => setActiveTab(tab as "details" | "drinks")}
            className={`cursor-pointer px-4 py-2 w-full text-center transition`}
            style={{
              ...(activeTab === tab
                ? {
                    ...TEXT_STYLES.READEX_PRO.SEMIBOLD.H6,
                    color: COLORS.BRAND_PRIMARY,
                    borderBottom: `2px solid ${COLORS.BRAND_PRIMARY}`,
                  }
                : {
                    ...TEXT_STYLES.READEX_PRO.MEDIUM.H6,
                    color: COLORS.GRAY,
                  }),
            }}
          >
            {tab === "details" ? "تفاصيل الكافيه" : "المشروبات"}
          </div>
        ))}
      </div>

      {/* Content */}
      <div className="mt-6">
        {/* DETAILS TAB */}
        {activeTab === "details" && (
          <div>
            <h3
              className="mb-2"
              style={{ ...TEXT_STYLES.READEX_PRO.SEMIBOLD.H5, color: COLORS.TEXT }}
            >
              وصف الكافيه
            </h3>
            <p
              style={{
                ...TEXT_STYLES.READEX_PRO.MEDIUM.H7,
                color: COLORS.GRAY,
              }}
            >
              كُوفي شوب أنيق يجمع بين الدفء والحداثة، مصمم بديكور يجمع بين الطابع
              الكلاسيكي واللمسات العصرية. يتميز بأجواء هادئة تناسب العمل والدراسة،
              مع موسيقى خفيفة تزيد من استرخاء الزوار.
            </p>

            {/* Reviews */}
            <div className="mt-10">
              <h3
                className="mb-4"
                style={{ ...TEXT_STYLES.READEX_PRO.SEMIBOLD.H5, color: COLORS.TEXT }}
              >
                التقييمات
              </h3>
              {reviews.map((review) => (
                <div key={review.id} className="flex flex-row-reverse gap-4 mb-6">
                  <Image
                    src={userPic}
                    alt={review.name}
                    width={48}
                    height={48}
                    className="rounded-full object-cover object-top w-12 h-12"
                  />
                  <div className="flex-1">
                    <div className="flex items-center justify-between flex-row-reverse">
                      <div>
                        <h4 style={{ ...TEXT_STYLES.READEX_PRO.SEMIBOLD.H6, color: COLORS.TEXT }}>
                          {review.name}
                        </h4>
                        <p style={{ ...TEXT_STYLES.READEX_PRO.MEDIUM.H8, color: COLORS.GRAY }}>
                          {review.title}
                        </p>
                      </div>
                      <div className="flex">
                        {[...Array(review.rating)].map((_, i) => (
                          <FaStar key={i} className="text-yellow-400" />
                        ))}
                      </div>
                    </div>
                    <p
                      className="mt-2"
                      style={{
                        ...TEXT_STYLES.READEX_PRO.MEDIUM.H7,
                        color: COLORS.GRAY,
                      }}
                    >
                      {review.comment}
                    </p>
                  </div>
                </div>
              ))}
            </div>
          </div>
        )}

        {/* DRINKS TAB */}
        {activeTab === "drinks" && (
          <div>
            {drinksData.map((item) => {
              const cartItem = cartItems.find((ci) => ci.id === item.id);
              return (
                <div
                  key={item.id}
                  className="flex items-center justify-between mb-4 p-4 rounded-xl border"
                  style={{ borderColor: COLORS.STROKE }}
                >
                  <div className="flex flex-col text-right">
                    <span style={{ ...TEXT_STYLES.READEX_PRO.SEMIBOLD.H6, color: COLORS.TEXT }}>
                      {item.name}
                    </span>
                    <span style={{ ...TEXT_STYLES.READEX_PRO.MEDIUM.H7, color: COLORS.GRAY }}>
                      {item.price} جنيه
                    </span>
                  </div>

                  {cartItem ? (
                    <div className="flex items-center gap-2">
                      <button
                        onClick={() => dispatch(decreaseQuantity(item.id))}
                        className="px-3 py-1 rounded"
                        style={{ backgroundColor: COLORS.STROKE }}
                      >
                        -
                      </button>
                      <span style={{ ...TEXT_STYLES.READEX_PRO.SEMIBOLD.H6, color: COLORS.TEXT }}>
                        {cartItem.quantity}
                      </span>
                      <button
                        onClick={() => dispatch(increaseQuantity(item.id))}
                        className="px-3 py-1 rounded text-white"
                        style={{ backgroundColor: COLORS.BRAND_PRIMARY }}
                      >
                        +
                      </button>
                    </div>
                  ) : (
                    <button
                      onClick={() => dispatch(addToCart(item))}
                      className="px-4 py-1 rounded text-white"
                      style={{ backgroundColor: COLORS.BRAND_PRIMARY, ...TEXT_STYLES.READEX_PRO.MEDIUM.H7 }}
                    >
                      أضف
                    </button>
                  )}
                </div>
              );
            })}

            {/* Total */}
            {cartItems.length > 0 && (
              <div className="flex justify-between items-center mt-8 pt-4 border-t" style={{ borderColor: COLORS.STROKE }}>
                <span style={{ ...TEXT_STYLES.READEX_PRO.SEMIBOLD.H5, color: COLORS.TEXT }}>
                  {totalCost} جنيه
                </span>
                <Button isMain text="اطلب الان" />
              </div>
            )}
          </div>
        )}
      </div>
    </div>
  );
};

export default Page;
